body {
    font-family: 'Open Sans', sans-serif;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.subtitleSection {
    height: 660px;
    background-image: url(./../img/Квадратцикл.png);
    background-repeat: no-repeat;
    background-size: 100% 660px;
    .mainSubtitle {
        height: 660px;
        display: flex;
        align-items: center;
        justify-content: center;
        .blockSubtitle {
            text-align: center;
            .mainTitle {
                font-weight: 800;
                font-size: 70px;
                line-height: 95px;
                letter-spacing: 0.6px;
                margin-bottom: 35px;
                color: #fff;
            }
            .btn {
                display: inline-flex;
                align-items: center;
                font-weight: 500;
                font-size: 18px;
                line-height: 21px;
                letter-spacing: 0.3px;
                text-decoration: none;
                padding: 12px 12px 12px 12px;
                text-transform: uppercase;
                background-color: #fff;
                color: #7A7363;
                img {
                    margin-left: 5px;
                }
            }
        }
    }
}

.toursTitle {
    font-weight: 700;
    font-size: 36px;
    line-height: 51px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    text-align: center;
    margin: 50px 0 30px 0;
    color: #414141;
}

.line {
    width: 100%;
    border: 1px solid #E8E6E6;
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 38px;
    line-height: 51px;
    text-decoration: none;
    text-transform: uppercase;
    padding: 15px 0 15px 0;
    transition: all 0.5s ease 0s;
    background-color: #E8E6E6;
    margin: 30px 0 0 0;
    color: #7A7363;
    svg {
        width: 31px;
        margin-left: 10px;
    }
}

.btn:hover {
    background: #D2CCBE;
    color: #fff;
}

.reviewsSection {
    .reviewsTitle {
        font-weight: 500;
        font-size: 38px;
        line-height: 45px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        text-align: center;
        margin: 80px 0 0 0;
        color: #414141;
    }
}